'use client';
import React, { useState } from 'react';
import { FileUpload } from '@/components/FileUpload';
import { UIPreview } from '@/components/UIPreview';
import { ValidationResults } from '@/components/ValidationResults';
import { KnowledgeBasePanel } from '@/components/KnowledgeBasePanel';
import { useUIGeneration } from '@/hooks/useUIGeneration';
import { Sparkles, Wand2, Loader, AlertCircle } from 'lucide-react';

export default function HomePage() {
  const [prompt, setPrompt] = useState('');
  const [selectedFile, setSelectedFile] = useState<File | null>(null);
  const [activeTab, setActiveTab] = useState<'generator' | 'knowledge'>('generator');
  
  const { isGenerating, generationResult, error, generateUI, clearResult } = useUIGeneration();

  const handleGenerate = () => {
    generateUI({ prompt: prompt.trim(), image: selectedFile });
  };

  const handleClear = () => {
    setPrompt('');
    setSelectedFile(null);
    clearResult();
  };

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Header */}
      <header className="bg-white border-b border-gray-200">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex items-center justify-between h-16">
            <div className="flex items-center space-x-3">
              <Sparkles className="w-8 h-8 text-blue-600" />
              <h1 className="text-2xl font-bold text-gray-900">
                RAG-Enhanced UI Generator
              </h1>
            </div>
            <div className="flex space-x-1">
              <button
                onClick={() => setActiveTab('generator')}
                className={`px-4 py-2 rounded-lg transition-colors ${
                  activeTab === 'generator'
                    ? 'bg-blue-100 text-blue-700'
                    : 'text-gray-600 hover:text-gray-900'
                }`}
              >
                Generator
              </button>
              <button
                onClick={() => setActiveTab('knowledge')}
                className={`px-4 py-2 rounded-lg transition-colors ${
                  activeTab === 'knowledge'
                    ? 'bg-blue-100 text-blue-700'
                    : 'text-gray-600 hover:text-gray-900'
                }`}
              >
                Knowledge Base
              </button>
            </div>
          </div>
        </div>
      </header>

      <main className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        {activeTab === 'generator' ? (
          <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
            {/* Input Panel */}
            <div className="space-y-6">
              <div className="bg-white rounded-lg border p-6">
                <h2 className="text-xl font-semibold mb-6 flex items-center space-x-2">
                  <Wand2 className="w-6 h-6 text-blue-600" />
                  <span>Generate UI</span>
                </h2>

                {/* Text Prompt */}
                <div className="mb-6">
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Describe your UI component
                  </label>
                  <textarea
                    value={prompt}
                    onChange={(e) => setPrompt(e.target.value)}
                    className="w-full p-4 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-blue-500 resize-none"
                    rows={4}
                    placeholder="e.g., Create a modern login form with email and password fields, rounded corners, and a gradient background..."
                    disabled={isGenerating}
                  />
                </div>

                {/* File Upload */}
                <div className="mb-6">
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Or upload a UI screenshot
                  </label>
                  <FileUpload
                    onFileSelect={setSelectedFile}
                    selectedFile={selectedFile}
                    disabled={isGenerating}
                  />
                </div>

                {/* Actions */}
                <div className="flex space-x-3">
                  <button
                    onClick={handleGenerate}
                    disabled={isGenerating || (!prompt.trim() && !selectedFile)}
                    className="flex-1 bg-blue-600 text-white py-3 px-6 rounded-lg hover:bg-blue-700 transition-colors disabled:opacity-50 disabled:cursor-not-allowed flex items-center justify-center space-x-2"
                  >
                    {isGenerating && <Loader className="w-5 h-5 animate-spin" />}
                    <span>{isGenerating ? 'Generating...' : 'Generate UI'}</span>
                  </button>
                  <button
                    onClick={handleClear}
                    disabled={isGenerating}
                    className="px-6 py-3 border border-gray-300 rounded-lg hover:bg-gray-50 transition-colors disabled:opacity-50"
                  >
                    Clear
                  </button>
                </div>

                {/* Error Display */}
                {error && (
                  <div className="mt-4 p-4 bg-red-50 border border-red-200 rounded-lg flex items-start space-x-3">
                    <AlertCircle className="w-5 h-5 text-red-600 mt-0.5" />
                    <div>
                      <h4 className="font-medium text-red-900">Generation Failed</h4>
                      <p className="text-sm text-red-700 mt-1">{error}</p>
                    </div>
                  </div>
                )}

                {/* Generation Info */}
                {generationResult && (
                  <div className="mt-4 p-4 bg-blue-50 border border-blue-200 rounded-lg">
                    <div className="flex items-center justify-between mb-2">
                      <h4 className="font-medium text-blue-900">Generation Complete</h4>
                      <span className="text-sm text-blue-700">
                        ID: {generationResult.id.slice(0, 8)}...
                      </span>
                    </div>
                    <div className="grid grid-cols-2 gap-4 text-sm">
                      <div>
                        <span className="text-blue-700">Type:</span>
                        <span className="ml-2 font-medium capitalize">{generationResult.type}</span>
                      </div>
                      <div>
                        <span className="text-blue-700">Patterns Used:</span>
                        <span className="ml-2 font-medium">{generationResult.retrieved_patterns}</span>
                      </div>
                    </div>
                  </div>
                )}
              </div>

              {/* Validation Results */}
              {generationResult && (
                <div className="bg-white rounded-lg border p-6">
                  <h3 className="text-lg font-semibold mb-4">Validation Results</h3>
                  <ValidationResults
                    validation={generationResult.validation}
                    qualityScore={generationResult.quality_score}
                  />
                </div>
              )}
            </div>

            {/* Preview Panel */}
            <div className="lg:sticky lg:top-8">
              {generationResult ? (
                <UIPreview
                  html={generationResult.html}
                  title="Generated UI Component"
                />
              ) : (
                <div className="bg-white rounded-lg border p-8 text-center">
                  <Wand2 className="w-16 h-16 text-gray-300 mx-auto mb-4" />
                  <h3 className="text-lg font-medium text-gray-900 mb-2">
                    Ready to Generate
                  </h3>
                  <p className="text-gray-600">
                    Provide a text description or upload an image to get started
                  </p>
                </div>
              )}
            </div>
          </div>
        ) : (
          <div className="max-w-2xl mx-auto">
            <KnowledgeBasePanel />
          </div>
        )}
      </main>
    </div>
  );
}
